.price-content {
  padding: 188px 21px 108px;

  background-image: url("../img/bg-inPrice-mobile.png");
  background-repeat: no-repeat;
  background-position: -400px 0;

  position: relative;

  overflow: hidden;

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-inPrice-tablet.png");
    background-position: -263px 0;
    padding: 168px 94px 250px;
  }

  @media (min-width: 1200px) {
    padding: 212px 200px 272px;
    background-image: url("../img/bg-inPrice-desktop.png");
    background-position: center;
  }

  @media (min-width: 1440px) {
    padding: 212px 285px 272px;
  }
}

.price-content::after {
  content: "";
  position: absolute;
  bottom: -55px;
  left: -40px;
  z-index: 99;

  width: 110px;
  height: 110px;
  background: radial-gradient(68.31% 68.31% at 50% 50%, #FFFFFF 0%, #CADEF9 100%), #FFFFFF;
  box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
  filter: blur(10px);
  border-radius: 100px;

  @media (min-width: $tablet-width) {
    width: 172px;
    height: 172px;

    bottom: -66px;
  }
}

.price-content__title {
  margin: 0;
  padding-bottom: 68px;

  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  /* identical to box height, or 30px */

  text-align: center;

  color: #FFFFFF;

  @media (min-width: $tablet-width) {
    font-size: 30px;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.price-content__list {
  @include list-reset;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    column-gap: 14.5px;
    row-gap: 16px;
  }

  @media (min-width: $desktop-width) {
    column-gap: 50px;
    row-gap: 66px;
  }
}

.price-content__item {
  background: #FFFFFF;
  box-shadow: 0px 20px 50px rgba(47, 55, 237, 0.12);
  border-radius: 10px;

  min-height: 350px;
  box-sizing: border-box;

  padding: 206px 10px 29px 52px;
  margin-bottom: 10px;

  position: relative;

  @media (min-width: $desktop-width) {
    min-height: 326px;

    &:first-child, &:nth-child(2) {
      min-height: 219px;
      padding: 56px 61px 20px 165px;

      & .price-content__text {
        padding-top: 7px;
      }
    }
  }
}

.price-content__item--video {
  padding: 56px 22px 78px 52px;

   & .price-content__subtitle {
    text-align: center;
  }
}

.price-content__item--chat {
  padding-top: 132px;
  padding-right: 43px;
}

.price-content__item::before {
  content: "";
  position: absolute;
  left: 45px;
  background-repeat: no-repeat;
}

.price-content__item--books::before {
  background: url("../img/study-book.png");
  width: 167px;
  height: 117.59px;
  top: 61px;
}

.price-content__item--subscribe::before {
  background: url("../img/book.png");
  background-size: contain;
  width: 80px;
  height: 80px;
  top: 43px;

  @media (min-width: $desktop-width) {
    top: 56px;
  }

}

.price-content__item--chat::before {
  background: url("../img/smile.png");
  background-size: contain;
  width: 72px;
  height: 72px;
  top: 42px;
}

.price-content__img {
  width: 177.83px;
  border-radius: 10px;
  display: block;
  margin: 0 auto 33px;

  @media (min-width: $desktop-width) {
    width: 295px;
    height: 166px;
  }
}

.price-content__subtitle {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: #2F80ED;

  margin: 0;
}

.price-content__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 143.55%;
  /* or 20px */

  color: #636363;

  margin: 0;
  padding-top: 16px;
}