.promo-block {
  padding: 70px 10px 56px;
  color: $white;
  position: relative;

  @media (min-width: 450px) {
    text-align: center;
  }

  @media (min-width: $tablet-width) {
    text-align: left;
    margin-bottom: 130px;
  }
}

.promo-block::before {
  content: "";
  position: absolute;
  top: -50px;
  right: 0;
  width: 100%;
  height: 881px;
  background-image: url("../img/bg-main-tablet@1x.png");
  background-repeat: no-repeat;
  background-position: 65% 0;
  z-index: -1;

  @media (min-width: $tablet-width) {
    background-position: 100% 0;
    top: -90px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg-main-desktop@1x.png");
    height: 717px;
  }
}

.promo-block__inner {
  @media (min-width: $tablet-width) {
    width: 450px;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    box-sizing: border-box;
    padding-right: 200px;
  }
}

.promo-block__location {
  font-weight: 500;
  font-size: 12px;
  line-height: 124.41%;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  margin: 0;

  @media (min-width: $tablet-width) {
    font-style: 14px;
  }

  @media (min-width: $desktop-width) {
    font-style: 16px;
  }
}

.promo-block__title {
  margin: 0;
  padding: 18px 0 41px;

  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;

  @media (min-width: 450px) {
    width: 300px;
    margin: 0 auto;
  }

  @media (min-width: $tablet-width) {
    font-size: 36px;
    width: auto;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 40px;
  }
}

.promo-block__text {
  margin: 0;
  padding-bottom: 30px;

  font-size: 14px;
  line-height: 124.41%;

  @media (min-width: $tablet-width) {
    font-size: 18px;
    padding-bottom: 36px;
  }
}

.promo-block__button {
  width: 223px;
  height: 77px;
  background: #64E2CD;
  border-radius: 50px;
  border: 14px solid rgba(100, 226, 205, 0.5);
  background-clip: padding-box;

  font-weight: bold;
  font-size: 14px;
  line-height: 124.41%;
  color: $white;
  text-transform: uppercase;

  margin-bottom: 53px;
}

.promo-block__button:hover {
  background: #2ac7b2;
}

.promo-block__rating {
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    position: absolute;
    right: 493px;
    bottom: -80px;
  }

  @media (min-width: $desktop-width) {
    left: 0;
  }

  @media (min-width: 1300px) {
    right: 1025px;
  }
}
