.program {
  position: relative;

  @media (min-width: $tablet-width) {
    margin: 0 145px 0 94px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto 1fr 1fr;
  }

  @media (min-width: $desktop-width) {
    margin: 0 173px 0 235px;
    grid-template-columns: auto 330px;
    grid-template-rows: auto 1fr auto;
    column-gap: 70px;
  }
}

.program__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  text-align: center;

  margin: 0;
  padding-bottom: 80px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 124.41%;
    padding-left: 0;
    padding-top: 35px;
    padding-bottom: 69px;
    
    text-align: left;

    grid-column: 1/3;
  }

  @media (min-width: $desktop-width) {
    padding-top: 100px;
    padding-bottom: 45px;

  }
}

.program__list {
  @include list-reset;

  padding: 0 20px 0 70px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0;

    grid-column: 1/3;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;
  }
}

.program__item {
  padding-bottom: 40px;

  &:last-child {
    padding-bottom: 35px;
  }

  @media (min-width: $tablet-width) {
    width: calc(50% - 30px);
    margin-right: 30px;
    padding-bottom: 82px;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:nth-child(3) {
      padding-bottom: 50px;
    }
  }
}

.program__subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 143.55%;
  /* or 20px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $blue;

  margin: 0;
  padding-bottom: 10px;
}

.program__description {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 143.55%;

  color: $gray;

  margin: 0;

  @media (min-width: $tablet-width) {
    padding-right: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 28px;
  }
}

.program__link {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 200.05%;
  /* or 28px */

  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration-style: dashed;

  color: $blue;

  display: inline-block;
  padding: 0 20px 0 70px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    width: 250px;
  }

  @media (min-width: $desktop-width) {
    width: auto;
  }
}

.program__add {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;

    position: relative;
    top: -35px;

    width: 280px;
    height: 212px;

    background: #FFFFFF;
    box-shadow: 0px 30px 100px rgba(47, 55, 237, 0.1);
    border-radius: 10px;

    padding: 58px 12px 60px 39px;

    box-sizing: border-box;
  }

  @media (min-width: 939px) {
    top: -10px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 2/3;

    width: 330px;
    height: 212px;

    padding: 60px;
    padding-right: 40px;
  }
}

.program__add::after {
  content: "";
  position: absolute;
  top: -45px;
  right: -45px;

  width: 89px;
  height: 89px;

  background: radial-gradient(68.31% 68.31% at 50% 50%, #6AAAFF 0%, #2F80ED 100%), #FFFFFF;
  box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
  filter: blur(10px);

  border-radius: 50px;
}

.program__add::before {
  content: "";
  position: absolute;
  top: -62px;
  right: -62px;

  width: 125px;
  height: 125px;

  background: radial-gradient(68.31% 68.31% at 50% 50%, #6AAAFF 0%, #2F80ED 100%), #FFFFFF;
  opacity: 0.1;
  box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);

  border-radius: 80px;
}