  .up-button {
  color: #00bfff;
  border: none;
  
  text-align: center;
  font-size: 54px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: opacity .5s, visibility .5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}

.up-button:hover {
  cursor: pointer;
  color: $black;
}
.up-button:active {
  color: $black;
}
.up-button--show {
  opacity: 1;
  visibility: visible;
}