.copyright {
  text-align: center;

  padding: 42px 0 46px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 119.62%;
  /* identical to box height, or 17px */

  text-align: center;

  color: #FFFFFF;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 18px 16px;

    text-align: left;
  } 

  @media (min-width: $desktop-width) {
    padding: 29px 134px;
  }
}

.copyright__link-wrapper {

  @media (min-width: $tablet-width) {
    order: 1;
  }

  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.copyright__link {
  display: block;
  margin: 0;
  padding-bottom: 7px;

  opacity: 0.7;
  text-decoration: none;
  color: #FFFFFF;

  @media (min-width: $desktop-width) {
    margin-right: 49px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.copyright__text {
  opacity: 0.7;

  margin: 0;
  padding-top: 33px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }
}