.message__wrapper {
  background-color: #fff;
  max-width: 290px;
  position: relative;
  padding: 60px 27px;
  text-align: center;
  box-sizing: border-box;

  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 10px;

  overflow: hidden;

  @media (min-width: $tablet-width) {
    max-width: 502px;
    padding: 70px 114px 43px;
  }

  @media (min-width: $desktop-width) {
    max-width: 570px;
    padding: 70px 147px 43px;
  }
}

.message__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;

  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 28px;
    line-height: 33px;
  }
}

.message__message {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;

  margin: 0;
  padding: 20px 0 30px;

  @media (min-width: $tablet-width) {
    padding: 25px 0 50px;
  }
}

.message__btn {
  border: none;
  width: 122px;
  height: 50px;
  box-sizing: border-box;
  margin: 0 auto;
  padding-left: 34px;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
}
