.result {
  background-image: url("../img/bg-result-mobile.png");
  background-repeat: no-repeat;
  background-position: -260px 0;

  padding: 260px 10px 167px;

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-result-tablet.png");
    padding: 230px 105px 300px 96px;
    background-position: -260px -20px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/result-bg.png");
    padding: 178px 135px 168px;
    background-position: left 60px;
  }
}

.result__title {
  color: $white;

  margin: 0;
  padding-bottom: 42px;

  text-align: center;
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;

  @media (min-width: $tablet-width) {
    font-size: 30px;

    text-align: left;
  }
}

.result__list {
  @include list-reset;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
  }
}

.result__item {
  background: $white;
  box-shadow: 0px 10px 30px rgba(47, 55, 237, 0.1);
  border-radius: 10px;

  box-sizing: border-box;
  padding: 152px 42px 55px 62px;
  margin-bottom: 10px;

  position: relative;

  font-size: 14px;
  line-height: 143.55%;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
    padding: 56px 50px 0 152px;
    height: 278.47px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 36px);
    margin-right: 36px;
    margin-bottom: 41px;
    padding: 35px 46px 23px 100px;
    

    &:nth-child(2n) {
      margin-right: 0;
    }

    @media (min-width: 1440px) {
      font-size: 16px;
      padding: 56px 49px 50px 151px;
    }
  }
} 

.result__item::before {
  content: "";
  width: 60px;
  height: 60px;
  position: absolute;
  top: 56px;

  background-repeat: no-repeat;
  background-size: contain;

  @media (min-width: $tablet-width) {
    left: 60px;
  }

  @media (min-width: $desktop-width) {
    left: 25px;
  }

  @media (min-width: 1440px) {
    left: 60px;
  }
}

.result__item--process::before {
  background-image: url("../img/process.jpg");
}

.result__item--tools::before {
  background-image: url("../img/tools.jpg");
}

.result__item--idea::before {
  background-image: url("../img/idea.jpg");
}

.result__item--case::before {
  background-image: url("../img/case.jpg");
}

.result__subtitle {
  font-style: normal;
  font-weight: 600;
  
  /* or 20px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $blue;

  margin: 0;
  padding-bottom: 26px;

  @media (min-width: $tablet-width) {
    padding-bottom: 13px;
  }
}

.result__description {
  font-weight: normal;
 
  line-height: 182.05%;
  /* or 25px */

  color: $gray;

  margin: 0;
}