.users {
  background-image: url("../img/users-bg.png");
  background-repeat: no-repeat;
  background-position: 8%;
  background-size: cover;

  padding: 49px 0 131px;

  @media (min-width: $tablet-width) {
    padding: 75px 0 150px;
    background-size: 100% 100%;
  }

  @media (min-width: $desktop-width) {
    margin-left: 65px;
    padding-left: 268px;
    padding-top: 129px;
    padding-bottom: 160px;
    position: relative;
    overflow: hidden;
  }
}

.users::before {
  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    right: -30px;
    top: 0;

    width: 231px;
    height: 231px;

    background: radial-gradient(68.31% 68.31% at 50% 50%, #FFFFFF 0%, #CADEF9 100%), #FFFFFF;
    box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
    filter: blur(20px);
    border-radius: 120px;
  }
}

.users__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;

  margin: 0;
  padding: 0 30px 39px 70px;

  @media (min-width: $tablet-width) {
    padding-left: 94px;
    width: 350px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
    width: auto;
    font-size: 30px;
    padding-bottom: 60px;
  }
}

.users__list {
  @include list-reset;
  padding: 0 70px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;

    padding: 0 94px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
    padding-right: 235px;
  }
}

.users__item {
  margin-bottom: 24px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-right: 40px;
    width: calc(35% - 40px);

    &:last-child {
      margin-right: 0;
    }
  }

  @media (min-width: $desktop-width) {
    margin-right: 100px;
    width: calc(35% - 100px);
  }

}

.users__subtitle {
  font-weight: 600;
  font-size: 14px;
  line-height: 143.55%;
  /* or 20px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $blue;

  margin: 0;
  padding-bottom: 8px;
}

.users__description {
  font-weight: normal;
  font-size: 14px;
  line-height: 143.55%;
  /* or 20px */
  color: #636363;

  margin: 0;
}
