.gallery__wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;

  @media (min-width: $tablet-width) {
    grid-template-columns: repeat(4, 1fr);
  }

  @media (min-width: $desktop-width) {
    column-gap: 25px;
    row-gap: 23px;
  }
}

.gallery__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  /* or 30px */

  text-align: center;

  color: #000000;

  margin: 0;
  padding: 43px 40px 30px;

  @media (min-width: $tablet-width) {
    padding: 50px 0 70px;
    font-size: 30px;
  }

  @media (min-width: $desktop-width) {
    padding: 100px 0 70px;
  }
}

.gallery__image {
  width: 100%;
}