.rating {
  color: $blue;
  box-sizing: border-box;

  background: radial-gradient(68.31% 68.31% at 50% 50%, #F9FBFF 0%, #CADEF9 100%), #FFFFFF;
  box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);

  width: 231px;
  height: 231px;

  border-radius: 120px;

  text-align: center;
  padding: 38px 0;
}

.rating__title {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 27px;
}

.rating__value {
  margin: 0;
  font-weight: bold;
  font-size: 34px;
  line-height: 66px;
}

.rating__author {
  margin: 0;
  font-size: 14px;
  line-height: 26px;

  & span {
    display: block;
    font-weight: bold;
  }
}

.rating__link {
  font-size: 10px;
  line-height: 20px;
  text-decoration: none;
  color: $blue;
}
