.button {
  background: #52D1BC;
  max-height: 50px;
  border-radius: 50px;
  border: none;

  font-weight: bold;
  font-size: 14px;
  line-height: 22px;
  text-align: center;

  color: #FFFFFF;

  text-transform: uppercase;

  padding: 16px 41px;
}

.button:hover {
  background: $blue;
}

.button:active {
  color: #20ab94;
  border: solid 1px #52D1BC;
  background-color: $white;
  opacity: 1;
}