.advantages {
  margin-bottom: 66px;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 0 135px;
    align-items: center;
  }
}

.advantages__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  text-align: center;
  margin: 0;
  padding: 0 8px 67px;

  & span {
    display: block;
  }

  @media (min-width: $tablet-width) {
    font-size: 30px;
    width: 410px;
    margin: 0 auto;
    padding: 0 0 60px;

    & span {
      display: inline;
    }
  }

  @media (min-width: $desktop-width) {
    width: 40%;
    text-align: left;
    margin-right: 30px;
  }
}

.advantages__list {
  @include list-reset;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 0 94px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    width: 60%;
  }
}

.advantages__item {
  box-shadow: 0px 30px 100px rgba(47, 55, 237, 0.1);
  border-radius: 10px;

  height: 234.84px;
  box-sizing: border-box;

  padding: 38px 20px 50px 54px;
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    height: 221px;
    padding: 34px 10px 30px 40px;
  }
}

.advantages__percent {
  font-weight: bold;
  font-size: 24px;
  line-height: 143.55%;
  color: $blue;

  margin: 0;
  padding-bottom: 13px;
}

.advantages__description {
  font-size: 14px;
  line-height: 143.55%;

  margin: 0;
  color: #636363;
}
