.header {
  position: relative;
  min-height: 50px;

  @media (min-width: $tablet-width) {
    padding-top: 40px;
    padding-left: 16px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 30px 120px 0;
    align-items: center;
  }
}



.header--opened {
  background-color: $blue;
  padding-left: 0;
  padding-top: 0;

  & .header__button {
    display: none;
  }
  & .header__cities {
    display: none;
  }

  @media (min-width: $desktop-width) {
    padding: 30px 120px 0;
    background-color: transparent;

  & .header__button {
    display: block;
    }
  & .header__cities {
    display: block;
    }
  }
}

.header__cities {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline-block;
    position: relative;
  }
}

.header__cities::after {
  content: "";
  position: absolute;
  right: -20px;
  top: 7px;

  border-style: solid;
  border-width: 5px 5px 5px 5px; 
  border-color: $blue transparent transparent transparent; 
  width: 0;
  height: 0;

  @media (min-width: $desktop-width) {
    border-color: $white transparent transparent transparent; 
  }

  @media (min-width: 1440px) {
    border-color: #52D1BC transparent transparent transparent; 
  }
}

.header__submenu {
  position: absolute;
  width: 100%;
  z-index: 10;
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;

  @include list-reset;

  & li {
    margin-top: 10px;
  }

  & a {
    text-decoration: none;
    font-size: 14px;
    line-height: 119.62%;
    color: $blue;
    padding-top: 10px;

    @media (min-width: $desktop-width) {
      color: $white;
    }

    @media (min-width: 1340px) {
      color: $blue;
    }
  }
}

.header__site-nav {
  @media (min-width: $desktop-width) {
    margin-left: auto;
    margin-right: 226px;
  }
}

.header__city {
  font-weight: bold;
  font-size: 14px;
  line-height: 119.62%;
  color: $blue;
  margin: 0;
  text-transform: uppercase;

  @media (min-width: $desktop-width) {
    color: $white;
  }

  @media (min-width: 1340px) {
    color: $blue;
  }
}

.header__button {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 143.55%;
  background-color: transparent;
  color: $white;
  margin-left: 10px;
  margin-top: 25px;
  text-decoration: none;
  border: none;

  position: absolute;
  right: 200px;

  @media (min-width: $tablet-width) {
    right: 367px;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    right: 135px;
    color: $blue;
    background-color:$white;
    border-radius: 50px;
    padding: 11px 23px;
  }
}

.header__button:hover {
  background-color: $blue;
  color: $white;
  border: $white solid 1px;
}



