.reasons {
  padding: 0 8px;

  @media (min-width: $tablet-width) {
    padding-left: 91px;
    padding-right: 101px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 135px;
  }
}

.reasons__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  /* or 30px */

  text-align: center;
  margin: 0;
  padding: 50px 0;

  @media (min-width: $tablet-width) {
    padding-top: 40px;
    padding-bottom: 65px;
    text-align: left;

    font-size: 30px;
  }

  @media (min-width: $desktop-width) {
    font-size: 35px;
    padding-bottom: 80px;
  }
}

.reasons__img-wrapper {
  padding-bottom: 71px;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    align-items: center;

    padding-bottom: 77px;
  }
}

.reasons__img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.reasons__author-block {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    min-width: 351px;

    box-shadow: 0px 30px 100px rgba(47, 55, 237, 0.1);
    border-radius: 10px;

    height: 212px;
    margin-left: 92px;
    
    box-sizing: border-box;
    padding: 66px 63px;
  }
}

.reasons__author-name {
  font-weight: bold;
  font-size: 24px;
  line-height: 44px;

  color: #2F80ED;
  margin: 0;

  & + p {
    font-weight: normal;
    font-size: 16px;
    line-height: 29px;

    color: #636363;

    margin: 0;
  }
}

.reasons__list {
  @include list-reset;
  padding: 0 49px 0 71px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 131px;
  }
}

.reasons__item {
  padding-bottom: 40px;
  position: relative;

  @media (min-width: $tablet-width) {
    width: calc(50% - 87px);
    margin-right: 87px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  @media (min-width: $desktop-width) {
    width: calc(25% - 80px);
    margin-right: 80px;
    padding-bottom: 0;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:last-child {
      margin-right: 0;
    }

  }
}

.reasons__item::before {
  content: "";
  position: absolute;
  top: 4px;
  width: 60px;
  height: 56px;

  background-repeat: no-repeat;
  background-size: contain;
}

.reasons__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 18px;
  width: 70px;
  height: 70px;
  background: #2F80ED;
  opacity: 0.1;
  border-radius: 35px;
}

.reasons__item--bullhorn::before {
  background-image: url("../img/bullhorn.png");
  height: 61px;
}

.reasons__item--chat::before {
  background-image: url("../img/chat.png");
}

.reasons__item--online-class::before {
  background-image: url("../img/online-class.png");
  height: 61px;
}

.reasons__item--lecture::before {
  background-image: url("../img/lecture.png");
}

.reasons__subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 143.55%;
  /* or 20px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $blue;

  margin: 0;
  padding-top: 81px;
  padding-bottom: 16px;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    padding-top: 98px;
  }
}

.reasons__description {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 143.55%;
  /* or 23px */


  color: $gray;
}