.footer__cities {
  text-align: center;
  background: #2F80ED;

  padding: 44px 0 46px;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;

    padding: 50px 16px;
  }

  @media (min-width: $desktop-width) {
    padding: 41px 134px;
  }
}

.footer__copyright {
  background: #185FBF;
}

.footer__cities-list {
  @include list-reset;

  padding-bottom: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
  }
}

.footer__item {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-right: 26px;
    margin-bottom: 0;

    &:last-child {
      margin-right: 0;
    }
  }
}

.footer__city{
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 119.62%;
  /* identical to box height, or 17px */

  text-align: center;

  color: #FFFFFF;

  margin: 0;

  &--active {
    font-weight: bold;
  }
}

.footer__city--active {
  text-transform: uppercase;
}

.footer__button {
  padding: 13px 25px 13px;

  font-weight: normal;
  font-size: 14px;
  line-height: 119.62%;
  /* identical to box height, or 17px */

  color: #2F80ED;
  background-color: $white;
  text-transform: none;
  text-decoration: none;
}

.footer__button:hover {
  background-color: #1555e8;
  color: $white;
  border: solid 1px $white;
}