.site-nav {
  position: relative;
}

.site-nav__list {
  @include list-reset;
  padding-top: 0;

  @media (min-width: $desktop-width) {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.site-nav__item {
  text-align: center;
  font-weight: normal;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.09em;
  padding: 15px 0;
  border-top: 1px solid $white;
  background-color: $blue;

  &:last-child {
    border-bottom: 1px solid $white;
  }

  @media (min-width: $tablet-width) {
    padding: 32px 0;
    font-size: 24px;
    line-height: 25px;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
    font-weight: normal;
    font-size: 14px;
    line-height: 119.62%;
    padding-right: 39px;
    padding-top: 0;
    padding-bottom: 0;
    border: none;

    &:last-child {
      padding-right: 0;
      border: none;

    }
  }
}

.site-nav__link {
  text-decoration: none;
  color: $white;
  position: relative;
}

.site-nav__link:hover {
  text-decoration: underline;
}

.site-nav__link:active {
  text-decoration: underline;
}

.site-nav__toggle {
  position: absolute;
  top: 15px;
  right: 0;

  margin-right: 25px;

  display: block;
  width: 30px;
  height: 25px;
  background-color: transparent;
  cursor: pointer;
  border: none;

  @media (min-width: $tablet-width) {
    top: -25px;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.site-nav--closed .site-nav__toggle::before {
  content: "";

  position: absolute;
  top: 5px;
  left: 0;

  width: 30px;
  height: 25px;

  background-image: url("../img/burger.png");
  background-size: cover;
  background-repeat: no-repeat;
}

@media (min-width: $desktop-width) {
  .site-nav--closed .site-nav__toggle {
    right: 96px;
  }
}

.site-nav--opened .site-nav__toggle {
  width: 21px;
  height: 21px;

  @media (min-width: $tablet-width) {
    top: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 28px;
    height: 28px;
    top: -59px;
    right: 69px;
  }

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 7px;
    right: 0px;
    width: 21px;
    height: 2px;
    background-color: rgba(255, 255, 255, 0.5);

    @media (min-width: $desktop-width) {
      width: 28px;
    }
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: $white;
  }
}

@media (max-width: $desktop-width) {

  .site-nav--closed .site-nav__list {
    display: none;
  }


  .site-nav--nojs .site-nav__toggle {
    display: none;
  }
}



