.review {
  padding: 84px 8px 195px;

  background-image: url("../img/bg-review-mobile.png");
  background-repeat: no-repeat;

  background-position: -26px 0;

  @media (min-width: $tablet-width) {
    padding: 94px 120px 250px 94px;
    background-image: url("../img/bg-review-tablet.png");
    background-position: 12px 0;
    background-size: cover;
  }

  @media (min-width: $desktop-width) {
    padding: 70px 135px 130px;
    background-image: url("../img/bg-review-desktop.png");
    background-position: 74px 0;
    background-size: auto;

    position: relative;
  }
}


.review__title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  /* or 30px */

  text-align: center;

  color: $black;

  margin: 0;
  padding-bottom: 52px;

  @media (min-width: $tablet-width) {
    text-align: left;

    font-size: 30px;
    padding-bottom: 72px;
  }

  @media (min-width: $desktop-width) {
    font-size: 35px;
    padding-bottom: 77px;
  }
}

.review__list {
  @include list-reset;


  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    column-gap: 57px;
  }
}

.review__item {
  padding: 40px 34px 40px 51px;
  box-shadow: 0px 10px 30px rgba(47, 55, 237, 0.1);
  background-color: $white;
  border-radius: 10px;

  margin-bottom: 142px;

  position: relative;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    padding-right: 78px;
  }  

  @media (min-width: $desktop-width) {
    padding-right: 100px;
    padding-top: 27px;

    &:last-child {
    margin-bottom: 120px;
  }
  }
}

.review__item--2 {
  @media (min-width: $desktop-width) {
    grid-row: 1/3;
    grid-column: 2/3;
    margin-bottom: 320px;
  }
}

.review__item::after {
  content: "";
  position: absolute;
  bottom: -30px;
  left: 25px;

  width: 0;
  height: 0;

  border: 10px solid transparent; 
  border-top: 20px solid $white;
}

.review__item::before { 
  content: "";
  position: absolute;
  bottom: -104px;
  left: 0;
  width: 74px;
  height: 74px;
  border-radius: 35px;

  background-repeat: no-repeat;
  background-position: 46% 18%;
}

.review__item--1::before {
  background-image: url("../img/review-1.jpg");
  background-position: 10%;
}

.review__item--2::before {
  background-image: url("../img/review-3.jpg");
}

.review__item--3::before {
  background-image: url("../img/review-2.jpg");
}

.review__name {
  font-weight: 600;
  font-size: 14px;
  line-height: 143.55%;
  /* identical to box height, or 20px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: #636363;

  margin: 0;
  padding-bottom: 15px;
}

.review__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 178.05%;
  /* or 25px */


  color: #636363;

  margin: 0;
}

.review__add-block {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    bottom: 85px;
    right: 140px;

    width: 276px;
    height: 276px;

    text-align: center;

    border-radius: 200px;
    background: radial-gradient(68.31% 68.31% at 50% 50%, #FFFFFF 0%, #CADEF9 100%), #FFFFFF;
    box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
  }
}

.review__add-text {
  font-weight: normal;
  font-size: 12px;
  line-height: 143.55%;
  /* or 17px */

  padding: 133px 85px 13px;
  margin: 0;

  color: $blue;

  position: relative;
}

.review__add-text::before {
  content: "";
  width: 54px;
  height: 54px;

  position: absolute;
  top: 50px;
  left: calc(50% - 27px);

  background-image: url("../img/paper.png");
  background-repeat: no-repeat;
}

.review__link {
  text-decoration: none;
  font-weight: normal;
  font-size: 10px;
  line-height: 20px;

  color: $blue;
}
