.form {
  padding: 108px 20px 93px;
  background: rgba(47, 128, 237, 0.05);

  @media (min-width: $tablet-width) {
    padding: 108px 0 132px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 84px;
  }  
}

.form--popup {
  padding: 0;
}

.form__popup {
  display: flex;
  flex-direction: column;
  background-color: $white;
  padding: 60px 20px;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  width: 290px;
  box-sizing: border-box;

  position: relative;

  & .form__button {
    width: auto;
    padding: 16px 30px;
  }

  @media (min-width: $tablet-width) {
    padding: 60px 30px;
    width: 400px;
    & .form__input {
      margin-bottom: 25px;
    }
  }
}

.form__wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 280px);
    column-gap: 20px;
    row-gap: 32px;

    justify-content: center;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 266px);
  }
}

.form__input {
  font-weight: 500;
  font-size: 14px;
  line-height: 124.41%;
  /* or 17px */

  color: $black;

  padding: 15px 20px 15px 39px;
  margin-bottom: 20px;

  background: #FFFFFF;
  border: 1px solid rgba(47, 128, 237, 0.3);
  box-sizing: border-box;

  &::placeholder {
    color: #C6C6C6;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.form__button {
  width: 280px;
  margin: 25px auto 0;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    width: 266px;
  }
}