.price-list {
  padding: 62px 21px 37px;

  background-image: url("../img/bg-price-mobile.png");
  background-repeat: no-repeat;

  background-position: -38px 238px;

  @media (min-width: $tablet-width) {
    padding: 75px 94px 50px;
    background-image: url("../img/bg-price-tablet.png");
    background-position: center;

    position: relative;

    overflow: hidden;
  }

  @media (min-width: 1200px) {
    padding: 104px 200px 112px;
    background-image: url("../img/bg-price-desktop.png");
    background-position: center;
  }

  @media (min-width: 1440px) {
    padding: 104px 268px 112px;
  }
}

.price-list::before {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    top: -50px;
    right: -37px;
    z-index: -99;

    width: 151px;
    height: 151px;
    background: radial-gradient(68.31% 68.31% at 50% 50%, #FFFFFF 0%, #CADEF9 100%), #FFFFFF;
    box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
    filter: blur(10px);
    border-radius: 100px;
  }

  @media (min-width: $desktop-width) {
    width: 231px;
    height: 231px;

    right: -45px;
    top: -39px;
  }
}

.price-list::after {
  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    bottom: 36px;
    left: -60px;

    width: 141px;
    height: 141px;
    border-radius: 100px;

    background: radial-gradient(68.31% 68.31% at 50% 50%, #6AAAFF 0%, #2F80ED 100%), #FFFFFF;
    box-shadow: 0px 10px 50px rgba(119, 157, 255, 0.3);
    filter: blur(10px);
  }
}

.price-list__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 124.41%;
  /* or 30px */

  text-align: center;

  color: #000000;

  margin: 0;
  padding-bottom: 45px;

  @media (min-width: $tablet-width) {
    padding-bottom: 58px;
    font-size: 30px;
  }
}

.price-list__list {
  @include list-reset;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 22px;
    row-gap: 30px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 293px 160px;

    row-gap: 43px;
    column-gap: 42px;
  }
}

.price-list__item {
  background: #FFFFFF;
  box-shadow: 0px 20px 50px rgba(47, 55, 237, 0.12);
  border-radius: 10px;

  min-height: 293px;
  box-sizing: border-box;

  padding: 71px 39px 64px;

  text-align: center;

  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    &:last-child {
      min-height: 160px;
      width: 570px;

      padding: 46px 85px 35px 82px;

      display: flex;

      grid-column: 1/4;

      justify-self: center;
      justify-content: space-between;
      align-items: center;

      & .price-list__content {
        padding-bottom: 0;
      }
    }
  }
}

.price-list__content {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 158.47%;

  color: #2F80ED;

  margin: 0;
  padding-bottom: 48px;
}

.price-list__price {
  display: block;

  font-weight: bold;
  font-size: 20px;
  line-height: 32px;

  padding-bottom: 12px;
}

.price-list__button {
  padding: 16px 41px;
}

.price-list__item--profit {
  position: relative;
}

.price-list__item--profit::before {
  content: "";
  position: absolute;
  top: 0;
  right: 3px;

  width: 64px;
  height: 61.68px;
  background-color: $blue;
  border-radius: 205.594px;
  
  padding-top: 27px;
  box-sizing: border-box;

  font-weight: bold;
  font-size: 8.22375px;
  line-height: 124.41%;
  /* or 10px */

  color: $white;

  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 93px;
    height: 90px;

    font-size: 12px;
    line-height: 15px;

    padding-top: 37px;

    top: -16px;
    right: -8px;
  }
}